import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
class SignUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
        //   firstname: '',
        //   lastname: '', 
          account:{firstname:'',lastname:''}, 
          accountNumber:'',
          requestResponse: ''
        }
      } 
      pushRequest=()=>{ 
          axios.post(`/createAccount`,this.state.account).then(Response => {
              console.log(Response);
          }).then(this.setState({accountNumber:Response.accountNumber})).then(console.log(this.state.accountNumber))

      } 
      
      setStates = (event) => {
        this.setState({ [event.target.name]: event.target.value });
      }
      handleSubmit = (event) => {
        event.preventDefault();
      }
   
  render() {
    return (
      <div className="App">
        {/* <header className="App-header">
          <p>erabhr</p>
        </header>  */}
        <form onSubmit={this.handleSubmit} className="form-inline">
          <label className="required"></label>
          <label>
            <input type="text" name='firstname' target='one' className="form-control inputArea" onChange={this.setStates} firstname={this.state.account.firstname} id="Firstname" placeholder="firstname *"></input>
            <input type="text" name='lastname' target='two' className="form-control inputArea" onChange={this.setStates} lastname={this.state.account.lastname} id="Lastname" placeholder="lastname *"></input>
          </label>
          <input type="submit" value="Submit" onClick={this.pushRequest} />
        </form> 
       
         
      </div>
    );
  }
}

export default SignUp;